import React, { useState } from 'react';
import './cart.scss';
import CartBlock from './cart_components/cart-block/CartBlock';

const Cart = ({ data }) => {
  const [isOpen, setOpen] = useState(false);
  const openCart = () => {
    document.body.classList.add('no-scroll');
    setOpen(true);
  };
  const closeCart = () => {
    document.body.classList.remove('no-scroll');
    setOpen(false);
  };

  return (
    <>
      {!isOpen && <button onClick={openCart} className="cart__btn" />}
      {isOpen && (
        <div className="cart__container">
          <button onClick={closeCart} className="cart__container__close" />
          <div onClick={closeCart} className="cart__container__overlay" />
          <CartBlock closeCart={closeCart} items={data} />
        </div>
      )}
    </>
  );
};

export default Cart;
