import React from 'react';
import './cart-button.scss';
import classnames from 'classnames';

const CartButton = ({ value, type, onClick, disable, className }) => (
  <button
    className={classnames('cart-button', {
      [className]: !!className,
      'cart-button_disable': disable,
      [`cart-button_${type}`]: !disable && type
    })}
    onClick={onClick}
  >
    {value}
  </button>
);

export default CartButton;
