import React from 'react';
import './cart-checkbox.scss';

const CartCheckbox = ({ text, checked, onChange }) => (
  <label className="cart-checkbox">
    <input
      onChange={onChange}
      checked={checked}
      className="cart-checkbox__input"
      type="checkbox"
    />
    <span className="cart-checkbox__checkmark" />
    <span className="cart-checkbox__text">{text}</span>
  </label>
);

export default CartCheckbox;
