import React from 'react';
import './cart-textarea.scss';
import classnames from 'classnames';

const CartTextarea = ({
  rows,
  value,
  id,
  placeholder,
  onChange,
  showWarning
}) => (
  <div
    className={classnames('cart-textarea', {
      'cart-textarea_warning': showWarning
    })}
  >
    <textarea
      required
      value={value}
      onChange={onChange}
      id={id}
      rows={rows}
      className="cart-textarea__input"
    />
    <label htmlFor={id} className="cart-textarea__label">
      {placeholder}
    </label>
  </div>
);

export default CartTextarea;
