import React, { Component } from 'react';
import './cart-block.scss';
import CartItems from '../cart-items/CartItems';
import CartTotal from '../cart-total/CartTotal';
import orderImg from '../../../../images/eastwood-done.png';
import classnames from 'classnames';

class CartBlock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOrdered: false,
      items: props.items
    };
  }

  changeOptions = (id, property, value) => {
    let { items } = this.state;

    items = items.map(item => {
      if (item.id === id) {
        let updatedItem = { ...item };
        updatedItem.options[property] = value;
        return updatedItem;
      }
      return item;
    });

    this.setState({ items });
  };

  removeProduct = id => {
    let { closeCart } = this.props;
    let { items } = this.state;

    items = items.filter(item => item.id !== id);

    this.setState({ items }, () => {
      if (items.length === 0) closeCart();
    });
  };

  makeOrder = () => {
    this.setState({ isOrdered: true });
  };

  render() {
    const { items, isOrdered } = this.state;
    const fullPrice = items.reduce(
      (prev, current) => prev + current.options.count * current.cost,
      0
    );

    return (
      <div className="cart-block">
        <div
          className={classnames('cart-block__title', {
            'cart-block__title_ordered': isOrdered
          })}
        >
          {isOrdered ? 'Ваш заказ оформлен' : 'Ваш заказ'}
          {isOrdered && (
            <img className="cart-block__order-img" src={orderImg} alt="" />
          )}
        </div>
        {!isOrdered && (
          <div className="cart-block__row">
            <CartItems
              removeProduct={this.removeProduct}
              changeOptions={this.changeOptions}
              items={items}
            />
            <CartTotal makeOrder={this.makeOrder} fullPrice={fullPrice} />
          </div>
        )}
      </div>
    );
  }
}

export default CartBlock;
