import React, { Component } from 'react';
import './cart-product.scss';
import CartOptions from '../cart-options/CartOptions';
import classnames from 'classnames';
import numberWithSpaces from '../../../../utils/numberWithSpaces';

class CartProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      readyForRemove: false,
      count: 5,
      timer: null
    };
  }

  onRemove = () => {
    const {
      removeProduct,
      product: { id }
    } = this.props;

    const intervalId = setInterval(() => {
      const { count, timer } = this.state;

      if (count === 0) {
        removeProduct(id);
        clearInterval(timer);
      } else {
        this.setState(prevState => ({
          ...prevState,
          count: --prevState.count
        }));
      }
    }, 1000);

    this.setState({ readyForRemove: true, timer: intervalId });
  };

  stopDeletion = () => {
    const { timer } = this.state;

    clearInterval(timer);
    this.setState({ readyForRemove: false, count: 5, timer: null });
  };

  render() {
    const { product, changeOptions } = this.props;
    const { readyForRemove, count } = this.state;

    return (
      <div
        className={classnames('cart-product', {
          'cart-product_remove': readyForRemove
        })}
      >
        {readyForRemove ? (
          <div className="cart-product__timer">
            <div className="cart-product__timer__left">
              <span className="cart-product__timer__counter">{count}</span>
              <span>Вы еще можете вернуть товар в корзину</span>
            </div>
            <button
              className="cart-product__timer__return"
              onClick={this.stopDeletion}
            >
              Вернуть
            </button>
          </div>
        ) : (
          <>
            <img
              src={product.imgs[product.options.color]}
              alt={product.name}
              className="cart-product__img"
            />
            <div className="cart-product__info">
              <div className="cart-product__info__main">
                <div className="cart-product__info__title">{product.name}</div>
                <div className="cart-product__info__cost">
                  {numberWithSpaces(product.cost * product.options.count)} руб.
                </div>
              </div>
              <CartOptions
                onChange={changeOptions}
                id={product.id}
                options={product.options}
              />
            </div>
            <button onClick={this.onRemove} className="cart-product__remove" />
          </>
        )}
      </div>
    );
  }
}

export default CartProduct;
