import React, { Component } from 'react';
import './cart-order.scss';
import CartInput from '../cart-input/CartInput';
import CartCheckbox from '../cart-checkbox/CartCheckbox';

class CartOrder extends Component {
  changeFio = e => {
    const { onChange } = this.props;
    onChange('fio', e.target.value);
  };

  changeEmail = e => {
    const { onChange } = this.props;
    onChange('email', e.target.value);
  };

  changePhone = e => {
    const { onChange } = this.props;
    onChange('phone', e.target.value);
  };

  render() {
    const {
      data,
      withDelivery,
      withPayment,
      togglePayment,
      toggleDelivery,
      showWarning
    } = this.props;
    return (
      <div className="cart-order">
        <form className="cart-order__form">
          <CartInput
            showWarning={showWarning && !data.fio}
            value={data.fio}
            onChange={this.changeFio}
            id="fio"
            placeholder="ФИО"
            type="text"
          />
          <CartInput
            showWarning={showWarning && !data.email}
            value={data.email}
            onChange={this.changeEmail}
            id="email"
            placeholder="Email"
            type="text"
          />
          <CartInput
            showWarning={showWarning && (!data.phone || data.phone.length < 18)}
            value={data.phone}
            onChange={this.changePhone}
            id="phone"
            disableMaskChar
            placeholder="Телефон"
            mask="+7 (999)-999-99-99"
          />
          <CartCheckbox
            onChange={toggleDelivery}
            checked={!withDelivery}
            text="Самовывоз"
          />
          <CartCheckbox
            onChange={togglePayment}
            checked={!withPayment}
            text="Оплата при получении"
          />
        </form>
      </div>
    );
  }
}

export default CartOrder;
