import React, { Component } from 'react';
import './cart-options.scss';
import Colors from '../../../../constants/colors';
import Sizes from '../../../../constants/sizes';

class CartOptions extends Component {
  counterInput = null;

  chengeColor = e => {
    const { onChange, id } = this.props;
    onChange(id, 'color', e.target.value);
  };

  chengeSize = e => {
    const { onChange, id } = this.props;
    onChange(id, 'size', e.target.value);
  };

  chengeCount = e => {
    const { onChange, id } = this.props;
    onChange(id, 'count', e.target.value);
  };

  onCountKeyDown = (e) => {
    if (e.keyCode === 13) {
      this.counterInput.blur();
    }
  }

  render() {
    const {
      options: { count, size, color }
    } = this.props;
    return (
      <div className="cart-options">
        <select
          defaultValue={color}
          onChange={this.chengeColor}
          className="cart-options__select cart-options__select_color"
        >
          {Object.keys(Colors).map(color => (
            <option key={color} value={color}>
              {Colors[color]}
            </option>
          ))}
        </select>
        <select
          defaultValue={size}
          onChange={this.chengeSize}
          className="cart-options__select cart-options__select_size"
        >
          {Sizes.map(size => (
            <option key={size} value={size}>
              {size}
            </option>
          ))}
        </select>
        <label className="cart-options__count">
          <span>Кол-во: </span>
          <input
            ref={ref => (this.counterInput = ref)}
            onChange={this.chengeCount}
            value={count}
            type="number"
            onKeyDown={this.onCountKeyDown}
            className="cart-options__count__input"
          />
        </label>
      </div>
    );
  }
}

export default CartOptions;
