import React, { Component } from 'react';
import classnames from 'classnames';
import './cart-total.scss';
import CartOrder from '../cart-order/CartOrder';
import CartDelivery from '../cart-delivery/CartDelivery';
import CartPayment from '../cart-payment/CartPayment';
import CartButton from '../cart-button/CartButton';
import numberWithSpaces from '../../../../utils/numberWithSpaces';

const TABS = {
  INFO: {
    name: 'Заказать',
    value: 'INFO'
  },
  DEL: {
    name: 'Доставка',
    value: 'DEL'
  },
  PAY: {
    name: 'Оплата',
    value: 'PAY'
  }
};

class CartTotal extends Component {
  state = {
    showWarning: false,
    isOrderLoading: false,
    tab: TABS.INFO.value,
    withPayment: true,
    withDelivery: true,
    orderData: {
      [TABS.INFO.value]: {
        fio: null,
        email: null,
        phone: null
      },
      [TABS.DEL.value]: {
        address: null,
        comment: null
      },
      [TABS.PAY.value]: {
        number: null,
        owner: null,
        date: null,
        cvc: null
      }
    }
  };

  setTab = tab => {
    const { withDelivery, withPayment } = this.state;

    if (
      (tab === TABS.DEL.value && !withDelivery) ||
      (tab === TABS.PAY.value && !withPayment)
    )
      return;
    this.setState({ tab });
  };

  isOrderValid = divide => {
    const {
      orderData: { INFO, DEL, PAY },
      withDelivery,
      withPayment
    } = this.state;

    let isOrderValid = true;
    let isDeliveryValid = true;
    let isPaymentValid = true;
    if (!INFO.fio || !INFO.email || !INFO.phone || INFO.phone.length < 18)
      isOrderValid = false;
    if (withDelivery && !DEL.address) isDeliveryValid = false;
    if (
      withPayment &&
      (!PAY.number ||
        PAY.number.length < 19 ||
        !PAY.owner ||
        !PAY.date ||
        PAY.date.length < 5 ||
        !PAY.cvc ||
        PAY.cvc.length < 3)
    )
      isPaymentValid = false;

    if (divide)
      return {
        [TABS.INFO.value]: isOrderValid,
        [TABS.DEL.value]: isDeliveryValid,
        [TABS.PAY.value]: isPaymentValid
      };

    return isOrderValid && isDeliveryValid && isPaymentValid;
  };

  changeData = (tab, property, value) => {
    this.setState(
      prevState => ({
        ...prevState,
        orderData: {
          ...prevState.orderData,
          [tab]: {
            ...prevState.orderData[tab],
            [property]: value
          }
        }
      }),
      () => {
        const { showWarning, tab } = this.state;
        if (showWarning && this.isOrderValid(true)[tab])
          this.setState({ showWarning: false });
      }
    );
  };

  togglePayment = () => {
    this.setState(prevState => ({
      ...prevState,
      withPayment: !prevState.withPayment
    }));
  };

  toggleDelivery = () => {
    this.setState(prevState => ({
      ...prevState,
      withDelivery: !prevState.withDelivery
    }));
  };

  goToNextTab = () => {
    const { tab, withDelivery, withPayment } = this.state;
    let nextTab = null;

    switch (tab) {
      case TABS.INFO.value:
        if (withDelivery) {
          nextTab = TABS.DEL.value;
        } else if (withPayment) nextTab = TABS.PAY.value;
        break;

      case TABS.DEL.value:
        if (withPayment) nextTab = TABS.PAY.value;
        break;

      default:
        break;
    }

    if (this.isOrderValid(true)[tab]) {
      this.setState({ tab: nextTab, showWarning: false });
    } else this.setState({ showWarning: true });
  };

  goToPrevTab = () => {
    const { tab, withDelivery } = this.state;

    switch (tab) {
      case TABS.PAY.value:
        if (withDelivery) {
          this.setState({ tab: TABS.DEL.value });
        } else this.setState({ tab: TABS.INFO.value });
        break;

      case TABS.DEL.value:
        this.setState({ tab: TABS.INFO.value });
        break;

      default:
        break;
    }
  };

  changeInfo = (property, value) => {
    this.changeData(TABS.INFO.value, property, value);
  };

  changeDel = (property, value) => {
    this.changeData(TABS.DEL.value, property, value);
  };

  changePay = (property, value) => {
    this.changeData(TABS.PAY.value, property, value);
  };

  makeOrder = () => {
    const { makeOrder } = this.props;

    if (this.isOrderValid()) {
      this.setState({ isOrderLoading: true }, () => {
        setTimeout(() => {
          makeOrder();
        }, 1000);
      });
    } else {
      this.setState({ showWarning: true });
    }
  };

  render() {
    const {
      tab,
      orderData,
      withDelivery,
      withPayment,
      isOrderLoading,
      showWarning
    } = this.state;
    const { fullPrice } = this.props;
    const infoValue = TABS.INFO.value;
    const delValue = TABS.DEL.value;
    const payValue = TABS.PAY.value;

    return (
      <div className="cart-total">
        <div className="tabs">
          <div className="tabs__nav">
            {Object.keys(TABS).map(tabKey => (
              <button
                key={tabKey}
                className={classnames('tabs__item', {
                  tabs__item_active: tab === TABS[tabKey].value,
                  tabs__item_disable:
                    (TABS[tabKey].value === delValue && !withDelivery) ||
                    (TABS[tabKey].value === payValue && !withPayment)
                })}
              >
                {TABS[tabKey].name}
              </button>
            ))}
          </div>
          <div className="tabs__body">
            {tab === infoValue && (
              <CartOrder
                showWarning={showWarning}
                data={orderData[infoValue]}
                onChange={this.changeInfo}
                withDelivery={withDelivery}
                withPayment={withPayment}
                togglePayment={this.togglePayment}
                toggleDelivery={this.toggleDelivery}
              />
            )}
            {tab === delValue && (
              <CartDelivery
                showWarning={showWarning}
                withPayment={withPayment}
                data={orderData[delValue]}
                onChange={this.changeDel}
              />
            )}
            {tab === payValue && (
              <CartPayment
                showWarning={showWarning}
                data={orderData[payValue]}
                onChange={this.changePay}
              />
            )}

            <div className="tabs__control">
              {tab !== TABS.INFO.value && (
                <CartButton
                  className="tabs__control__prev"
                  onClick={this.goToPrevTab}
                  type="simple"
                  value="Назад"
                />
              )}
              {((tab === TABS.INFO.value && (withDelivery || withPayment)) ||
                (tab === TABS.DEL.value && withPayment)) && (
                <CartButton
                  className="tabs__control__next"
                  onClick={this.goToNextTab}
                  type="simple"
                  value="Далее"
                />
              )}
              {((tab === TABS.DEL.value && !withPayment) ||
                (tab === TABS.INFO.value && !withPayment && !withDelivery) ||
                tab === TABS.PAY.value) && (
                <CartButton
                  className="tabs__control__next"
                  disable={!this.isOrderValid()}
                  onClick={this.makeOrder}
                  type={isOrderLoading ? 'loading' : 'special'}
                  value={withPayment ? 'Оплатить' : 'Заказать'}
                />
              )}
            </div>
          </div>
        </div>
        <div className="cart-total__warning">
          {showWarning && !this.isOrderValid() && 'Заполните обязательные поля'}
        </div>
        <div className="cart-total__price">
          <span>Итого: {numberWithSpaces(fullPrice)} руб.</span>
        </div>
      </div>
    );
  }
}

export default CartTotal;
