import React from 'react';
import './cart-input.scss';
import InputMask from 'react-input-mask';
import classnames from 'classnames';

const CartInput = ({
  placeholder,
  type,
  id,
  value,
  onChange,
  mask,
  formatChars,
  disableMaskChar,
  isUppercase,
  showWarning
}) => (
  <div
    className={classnames('cart-input', {
      'cart-input_warning': showWarning
    })}
  >
    {mask ? (
      <InputMask
        value={value || ''}
        maskChar={disableMaskChar ? null : '_'}
        id={id}
        className="cart-input__input"
        required
        mask={mask}
        onChange={onChange}
        formatChars={formatChars}
      />
    ) : (
      <input
        onChange={onChange}
        value={value || ''}
        id={id}
        type={type}
        required
        className={classnames('cart-input__input', {
          'cart-input__input_uppercase': isUppercase
        })}
      />
    )}
    <label htmlFor={id} className="cart-input__label">
      {placeholder}
    </label>
  </div>
);

export default CartInput;
